.dressInfoRow-inner {
  display: flex;
  flex-direction: column;
  padding-bottom: 30px;
}
.slider-container {
  display: flex;
  padding-left: 5vw;
  padding-right: 5vw;
  justify-content: flex-start;
  border-bottom: 0.15px solid rgb(255, 250, 250, 0.6);
}
.dressSort-info {
  padding-right: 8vw;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
}
.dressSort-info > i {
  cursor: pointer;
  font-size: 24px;
  margin-left: 20px;
  flex: 1;
  float: right;
  display: flex;
  justify-content: flex-end;
  opacity: 0.8;
}
.dressSort {
  margin-left: 3vw;
  font-size: 24px;
  font-weight: bold;
}
.slick-slider {
  display: flex;
  padding: 20px;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}
.slick-list {
  width: fit-content;
}
.slick-arrow {
  opacity: 0.6;
  transform: scale(1.6);
}
.slick-arrow:hover {
  opacity: 1;
}
.slick-dots {
  margin-bottom: 10px;
}
.slick-dots > li {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 50%;
  width: fit-content;
  height: fit-content;
  opacity: 0.6;
  transform: scale(0.65);
}
.slick-dots .slick-active {
  opacity: 0.85;
}

.dressImg-list_thumb_img {
  max-width: 15vw;
  min-width: 15vw;
  height: 30vh;
  border-radius: 8px;
  box-shadow: rgb(175, 174, 174) 0.2px 0.2px 0.2px 0.2px;
  will-change: transform;
  cursor: pointer;
}

@keyframes goToUP {
  0% {
    z-index: 0;
    transform: scale(1);
  }
  50% {
    z-index: 10;
    transform: scale(1) translate(0, 0.6vh);
    opacity: 0.8;
  }
  100% {
    z-index: 0;
    transform: scale(1);
  }
}
.dressImg-list_thumb_img:hover {
  animation: goToUP 0.7s linear forwards;
}
